import React, { Component } from "react";
import wrapper_clickFun from "../../components/wrapperClickFun";
import ListPageTable from "../../pages/listPageTable";

const API_form_name = "sampleRegistration";
const form_name = "Sample Registration List";
const Create_Update_Path = `/${process.env.REACT_APP_NAME}/${API_form_name}/CreateUpdate`;
const listAPI = `/${process.env.REACT_APP_NAME}/${API_form_name}/ListAPI/`;
const form_type = "Master";

const tableHeaderList = [
    { cn: "Document No", fv: "code", fn: "document_no", st: "search_box", sv: "document_no" },
    { cn: "Registration Date", fv: "Date", fn: "registration_date", st: "datepicker", sv: "registration_date" },
    { cn: "Confirmation Date", fv: "Date", fn: "confirmation_date", st: "datepicker", sv: "confirmation_date" },
    { cn: "Report Due Date", fv: "Date", fn: "report_due_date", st: "datepicker", sv: "report_due_date" },
    { cn: "Service Type", fv: "service_type", fn: "service_type", st: "search_box", sv: "service_type" },
    { cn: "Applicant Type", fv: "applicant_type",fn: "applicant_type",st: "search_box",sv: "applicant_type"},
    { cn: "Applicant Name", fv: "applicant_name",fn: "applicant_name",st: "search_box",sv: "applicant_name"},
    { cn: "Applicant Email", fv: "applicant_email",fn: "applicant_email",st: "search_box",sv: "applicant_email"},
    { cn: "Created By", fv: "User Name", fn: "created_by", st: "search_box", sv: "created_by__full_name"},
    { cn: "Created Date", fv: "Date", fn: "created_date", st: "datepicker", sv: "created_date" },
];

class listPage extends Component {
    render() {
        return (
            <div className="wrapper">
                <div className="content-wrapper" onClick={wrapper_clickFun.bind(this)}>
                    <ListPageTable tableHeaderList={tableHeaderList} listAPI={listAPI} to_page={Create_Update_Path} generateWS={true} form_name={form_name} apiFormName={API_form_name} form_type={form_type}></ListPageTable>
                </div>
            </div>
        );
    }
}

export default listPage;
